import React, { Component } from 'react';
import logo from './logo.png';

export default class DOG extends Component {
  render() {
    return (
      <div
        style={{
          position: 'fixed',
          top: 10,
          right: 10,
          height: 80,
          width: 100
        }}
      >
        <img className="DOG" src={logo} />
      </div>
    );
  }
}



